<template>
    <div>
        <div class="container">
            <div class="data-gallery-grid">
                <div class="operator-layout">
                    <a @click="prev" style="cursor:pointer"><span class="badge badge-info">上一张</span></a>
                    <a @click="next" style="cursor:pointer"><span class="badge badge-info">下一张</span></a>
                    <a v-for="(item, index) in operator" :key="index" @click="custom_operator(item)" style="cursor:pointer"><span class="badge badge-info">{{item}}</span></a>
                </div>
                <div class="data-and-tag-layout">
                    <div class="data-and-tag-cell" v-for="(item,index) in value" :key="index">
                        <div class="title-item">
                            {{item.title}}
                        </div>
                        <div class="data-item">
                            <img v-if="item.type == 'IMAGE' " :src="item.data" :style="item.style"/>
                            <p v-else>{{item.data}}</p>
                        </div>
                        <div class="tag-item">
                            <div v-for="(tag_name, tag_index) in tags" :key="tag_index" style="margin-right: 10px">
                                <label>{{tag_name}}</label>
                                <!-- <input v-if="item.tag.indexOf(tag_name) != -1" type="checkbox" checked @click="tagChange(index, tag_name, false)">
                                <input v-else type="checkbox" @click="tagChange(index, tag_name, true)"> -->
                                <!-- <input type="checkbox" v-model="item.tag"> -->
                                <input type="checkbox" :checked="item.tag.indexOf(tag_name) != -1" @click="tagChange(index, tag_name)">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "BrowserData",
    data(){
        return{
            value: [],
            operator: [],
            tags:[],
            step: 0,
        }
    },
    mounted: function(){   
        var _this = this;
        this.$axios.get(this.BASE_URL + '/browser-api/entry/', {
            params:{

            }
        }).then(function(res){
            _this.value = res.data.content['value']
            _this.operator = res.data.content['operator']
            _this.tags = res.data.content['tags']
            _this.step = res.data.content['step']

            for(var index = 0; index<_this.value.length; ++index){
                var long_size = _this.value[index]['width'];
                if(long_size < _this.value[index]['height']){
                    long_size = _this.value[index]['height'];
                }
                var scale = 1.0;
                if(long_size > 512){
                    scale = 512.0 / long_size;
                }

                _this.value[index]['style'] = 
                        'width:'+Math.ceil(_this.value[index]['width'] * scale).toString()+"px;"+"height:"+Math.ceil(_this.value[index]['height'] * scale).toString()+"px;";
                console.log(_this.value[index]['style'])

                // 绑定键盘
                document.onkeydown = function(e){
                    var key = window.event.keyCode;
                    if(key == 37){
                        // 左
                        _this.prev();
                    }
                    else if(key == 39){
                        // 右
                        _this.next();
                    }
                }
            }
         })
    },
    beforeDestroy: function(){
        document.onkeydown = function(e){
            var key = window.event.keyCode;
            // do nothing
            console.log('destroy document key')
        }
    },
    methods:{
        next: function(){
            var _this = this;
            var params = new URLSearchParams();            
            params.append('step', this.step);
            params.append('data', JSON.stringify(_this.value));

            this.$axios.post(this.BASE_URL + '/browser-api/next/', params).then(function(res){
                _this.value = res.data.content['value']
                _this.operator = res.data.content['operator']
                _this.tags = res.data.content['tags']
                _this.step = res.data.content['step']
                
                for(var index = 0; index<_this.value.length; ++index){
                    var long_size = _this.value[index]['width'];
                    if(long_size < _this.value[index]['height']){
                        long_size = _this.value[index]['height'];
                    }
                    var scale = 1.0;
                    if(long_size > 512){
                        scale = 512.0 / long_size;
                    }

                    _this.value[index]['style'] = 
                            'width:'+Math.ceil(_this.value[index]['width'] * scale).toString()+"px;"+"height:"+Math.ceil(_this.value[index]['height'] * scale).toString()+"px;";
                    console.log(_this.value[index]['style'])
                }

                _this.$forceUpdate();
            })
        },
        prev: function(){
            var _this = this;
            var params = new URLSearchParams();            
            params.append('step', this.step);
            params.append('data', JSON.stringify(_this.value));

            this.$axios.post(this.BASE_URL + '/browser-api/prev/', params).then(function(res){
                _this.value = res.data.content['value']
                _this.operator = res.data.content['operator']
                _this.tags = res.data.content['tags']
                _this.step = res.data.content['step']

                for(var index = 0; index<_this.value.length; ++index){
                    var long_size = _this.value[index]['width'];
                    if(long_size < _this.value[index]['height']){
                        long_size = _this.value[index]['height'];
                    }

                    var scale = 1.0;
                    if(long_size > 512){
                        scale = 512.0 / long_size;
                    }

                    _this.value[index]['style'] = 
                            'width:'+Math.ceil(_this.value[index]['width'] * scale).toString()+"px;"+"height:"+Math.ceil(_this.value[index]['height'] * scale).toString()+"px;";
                    console.log(_this.value[index]['style'])
                }

                _this.$forceUpdate();
            })
        },
        custom_operator: function(tag){
            // do nothing
        },
        tagChange: function(index, tag_name){
            var tag_index = this.value[index]['tag'].indexOf(tag_name);
            if(tag_index == -1){
                // 添加标记
                this.value[index]['tag'].push(tag_name);
            }
            else{
                // 删除标记
                this.value[index]['tag'].splice(tag_index, 1);
            }

            this.$forceUpdate();
        }
    }
}
</script>
<style scoped>
.data-gallery-grid{
    display: flex;
    flex-direction: column;
}
.operator-layout{
}
.data-and-tag-layout{
    display: flex;
    flex-direction: row;
}
.data-and-tag-cell{
    display: flex;
    flex-direction: column;
    margin-right: 20px;
}
.data-item{

}
.tag-item{
    display: flex;
    flex-direction: row;
    justify-content:flex-start;
}
</style>


