<template>
   <div>
        <div class="container">
            <div class="card-section-group">
                <div class="card-item">
                    <div class="card-item-key">
                    文件数
                    </div>
                    <div class="card-item-value">
                    {{file_num}}
                    </div>
                </div>
                <div class="card-item">
                    <div class="card-item-key">
                    文件夹数
                    </div>
                    <div class="card-item-value">
                    {{folder_num}}
                    </div>
                </div>
            </div>
            <div class="separator"></div>
            <div>
                <table class="n-table">
                    <thead class="n-table-head">
                        <tr>
                            <th style="width: 70%;padding-top:10px;padding-bottom:10px">名称</th>
                            <th>大小</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td><i class="fa fa-level-up" aria-hidden="true" style="color: #999;cursor: pointer" @click="bowerParent"></i></td>
                            <td></td>
                            <td></td>
                        </tr>
                    </tbody>
                </table>
                    <table class="n-table n-file-content">
                    <tbody>
                        <tr v-for="(item, index) in current_folder" :key="index" style="border: 1px solid #9999"> 
                            <td class="row-items">
                                <div>
                                    <i v-if="item.type == 'file'" class="fa fa-file-text-o" aria-hidden="true"></i>
                                    <i v-else class="fa fa-folder" aria-hidden="true" @click="bowserChild(item.path, item.name)" style="cursor:pointer"></i> 
                                </div>
                                <div v-if="item.type == 'file'">{{item.name}}</div>
                                <div v-else @click="bowserChild(item.path, item.name)" style="cursor:pointer">{{item.name}}</div>
                            </td>
                            <td style="text-align:right; padding-right: 20px;">
                                <div v-show="item.type == 'file'">{{item.size}}</div>
                            </td>
                            <td>
                                <i v-show="item.type == 'file'" class="fa fa-download" aria-hidden="true" style="color:#055fb3;cursor:pointer" @click="download(item.path)" ></i>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div >

            </div>
        </div>
    </div> 
</template>

<script>
export default {
    name: "BrowserFiles",
    data(){
        return{
            files_tree:[{
                name: ".",
                type: "folder",
                size: "",
                path: "code",
                parent: "",
                folder:[{
                    name: 'experiment.py',
                    size: '1KB',
                    type: 'file'
                },{
                    name: 'params.yaml',
                    type: 'file',
                    size: '0KB'
                },
                {
                    name: 'src',
                    type: 'folder',
                    size: '',
                    path: 'code/AA',
                    parent: "code",
                    folder: [
                        {
                            name: 'a.py',
                            size: '2KB',
                            type: 'file',
                        },
                        {
                            name: 'include',
                            type: 'folder',
                            size: "",
                            path: "code/AA/BB",
                            parent: "code/AA",
                            folder:[{
                                name: 'b.py',
                                size: '10KB',
                                type: 'file'
                            }]
                        }
                    ]
                }],
            }],
            current_folder:[],
            current_folder_parent: '',
            file_num: 0,
            folder_num: 0,
            code_size: 0,
        }
    },
    mounted: function(){
        var _this = this;
        this.$axios.get(this.BASE_URL + '/browser-api/file/', {
            params: {}
        }).then(function(res){
            var response = res.data.content;
            _this.files_tree = response.files_tree;

            _this.current_folder = _this.files_tree[0].folder;
            _this.current_folder_parent = '';
            console.log("in mounted")
            console.log(_this.current_folder_parent)
            var queue=[]
            for(var index=0; index<_this.files_tree.length; ++index){
                if(_this.files_tree[index].type == 'folder'){
                    queue.push(_this.files_tree[index]);
                }
            }

            while(queue.length != 0){
                var data = queue.pop();
                for(var index=0; index<data.folder.length; ++index){
                    if(data.folder[index].type == 'folder'){
                        queue.push(data.folder[index]);
                        _this.folder_num += 1;
                    }
                    else{
                        _this.file_num += 1;
                    }
                }
            }
        })
    },
    methods:{
        bowserChild: function(folder, folder_name){
            var queue = [];
            for(var index=0; index<this.files_tree.length; ++index){
                if(this.files_tree[index].type == 'folder'){
                    queue.push(this.files_tree[index]);
                }
            }

            console.log('look folder '+folder_name)
            while(queue.length != 0){
                var data = queue.pop();
                if(data.path == folder){
                    this.current_folder = data.folder;
                    if (folder == folder_name){
                        this.current_folder_parent = "";
                    }
                    else{
                        this.current_folder_parent = folder;
                    }
                    
                    console.log("in childe")
                    console.log(this.current_folder_parent)
                    return;
                }

                for(var index=0; index<data.folder.length; ++index){
                    if(data.folder[index].type == 'folder'){
                        queue.push(data.folder[index]);
                    }
                }
            }
            console.log("why here")
        },
        bowerParent: function(){
            console.log("current folder parent")
            console.log(this.current_folder_parent)
            if(this.current_folder_parent == ""){
                // 当前是根目录，不可以继续浏览
                this.current_folder = this.files_tree[0].folder;
                return;
            }

            var queue = [];
            for(var index=0; index<this.files_tree.length; ++index){
                if(this.files_tree[index].type == 'folder'){
                    queue.push(this.files_tree[index]);
                }
            }

            while(queue.length != 0){
                var data = queue.pop();
                if(data.path == this.current_folder_parent){
                    this.current_folder = data.folder;
                    if(data.path == data.name){
                        this.current_folder_parent = ""
                    }
                    else{
                        this.current_folder_parent = data.path.split("/")[0]
                    }
                    return;
                }

                for(var index=0; index<data.folder.length; ++index){
                    if(data.folder[index].type == 'folder'){
                        queue.push(data.folder[index]);
                    }
                }
            }

            this.current_folder = [];
        },
        download: function(file){
            this.$axios.get(this.BASE_URL + '/browser-api/download/', {
                params: {
                    path: file,
                },
                responseType: 'blob'
            }).then(function(res){
                const disposition = res.headers['content-disposition'];
                // 拿到文件名
                let filename = disposition.split('filename=')[1];
                let blob = new Blob([res.data]);
                
                const URL = window.URL || window.webkitURL;
                let objectUrl = URL.createObjectURL(blob);
                const a = document.createElement('a');
                a.href = objectUrl;
                a.download = filename;
                a.click();
                URL.revokeObjectURL(objectUrl);
            })
        }
    }
}
</script>

<style scoped>
    .separator {
    height: 1px;
    background-color: #ccc;
    margin-top: 10px;
    margin-bottom: 20px;
    width: 350px
}
.n-table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    color: #333;
    font-family: "Source Sans Pro";
    font-weight: 400;
    font-style: normal;
    font-size: 14px;
    line-height: inherit;
    background-color: #f2f2f2;
    width: 50%;
    border: 1px solid #9999;
    border-radius:5px 5px 0 0;
    text-align: left;
}
.n-file-content{
    background-color: white
}
.n-file-content td{
    padding-top: 8px;
    padding-bottom: 8px;
}
.n-table-head {
    font-family: "Source Sans Pro";
    font-weight: 700;
    font-style: normal;
    font-size: inherit;
    line-height: inherit;
}
.n-table th{
    padding-left: 10px;
    padding-top: 4px;
    padding-bottom: 4px;
}
.n-table td{
    padding-left: 10px;
}

.n-table .row-items{
    display: flex;
    color: #055fb3;
    font-weight: bolder;
}
.n-table .row-items>:last-child{
    margin-left: 20px;
}

</style>


