<template>
  <div id="app">
    <div class="jumbotron" style="margin-top: -60px">
      <h1 class="display-4" style="text-align: left">AntGo: 数据筛选</h1>
      <p class="lead" style="text-align: left">帮助机器学习开发者快速筛选数据集样本，诊断数据问题</p>
      <hr class="my-4">
      <p style="text-align: left">通过定制data_generator回调函数，实现定制化展示数据.</p>
      <button type="button" class="btn btn-lg" :class="{'btn-primary': view=='文件', 'btn-outline-primary': view=='数据'}" style="margin-right: 40px;" @click="switchView('文件')">文件视图</button>
      <button type="button" class="btn btn-lg" :class="{'btn-outline-primary': view=='文件', 'btn-primary': view=='数据'}" @click="switchView('数据')">数据视图</button>
    </div>
    <div class="container">
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      view: '文件'
    }
  },
  methods: {
    switchView: function(t){
      this.view = t;
      if(this.view == '文件'){
        this.$router.push({path: '/'});
      }
      else{
        this.$router.push({path: '/BrowserData'});
      }
    }
  },
  mounted: function(){
    // this.$axios.get('static/config.json').then(function(res){
    //   Vue.prototype.BASE_URL = res.data.BASE_URL;
    //   console.log(Vue.prototype.BASE_URL)
    // })
  }
}
</script>

<style>
@import '../static/css/style.css';
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



